<template>
  <div id="app">
    <img v-on:click="searcFn(val)" class="log" src="./assets/book.jpg">
    <h1>{{ msg }}</h1>
    <input type="text" v-model="val" name="bookIndex" placeholder="  书名、作者、ISBN"> 
    <button v-on:click="searcFn(val)" v-bind:disabled="!val"> 搜索 </button>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      msg: '《*\\ 豆瓣书城 /*》',
      val: ''
    }
  },
  methods:{
    searcFn(par){
      this.$router.push({path: '/bookinfo/'+par})
    }
  },
  created(){
    this.$router.push({path:'/booklist/南极'});
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 30px;
}
#app img{
  cursor: pointer;
}
body{
  background-color: rgba(215,65,23,0.3);  
}
.log{
  width: 200px;
  border-radius: 50%;
}
h1{
  color: rgba(49,99,74,0.8);
  cursor: default;
}
</style>



