<template>
  <div class="hello">
    <div class="box" v-if="JSON.stringify(bookd)!='{}'">
      <div>
        <h2 class="color_yg">{{ bookd.title }}</h2>
        <ul>
          <li>
            <img id="bookPic" v-bind:src="bookd.image">
          </li>
          <li>
            <p>作者：<a v-on:click="booklist(bookd.author)"> {{bookd.author}} </a></p>
            <p>出版社：{{bookd.publisher}}</p>
            <p>译者：{{bookd.translator}}</p>
            <p>出版年：{{bookd.pubdate}}</p>
            <p>页数：{{bookd.pages}}</p>
            <p>定价：{{bookd.price}}</p>
            <p>装帧：{{bookd.binding}}</p>
            <!-- <p>丛书：{{bookd.series.title}}</p> -->
            <p>丛书ISBN：{{bookd.isbn13}}</p>
          </li>
        </ul>
        <h3 class="color_yg">内容简介-----</h3>
        <div>
          <pre>{{bookd.summary}}</pre>
        </div>

        <h3 class="color_yg">作者简介-----</h3>
        <div>
          <pre>{{bookd.author_intro}}</pre>
        </div>

        <h3 class="color_yg">目录-----</h3>
        <div>
          <pre>{{bookd.catalog}}</pre>
        </div>

        <h3 class="color_yg">豆瓣成员常用的标签(共22个) -----</h3>
        <div>
          <ul>
            <li class="paddin_3" v-for="i in bookd.tags" >
              <a class="backColor"v-on:click="booklist(i.title)">{{i.title}}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import VueJsonp from 'vue-jsonp'
Vue.use(VueJsonp)

export default {
  name: 'BookInfo',
  data () {
    return {
      bookd:{},
      param:''
    }
  },
  beforeCreate(){
    let seft = this;
    setInterval(function(){ 
      seft.param = seft.$route.params.id;
    }, 20);
  },
  methods:{
    booklist(par){
      this.$router.push({path:'/booklist/'+par});
    }
  },
  watch:{
    param(){
      let seft = this;
      seft.param = seft.$route.params.id;
      Vue.jsonp('https://api.douban.com/v2/book/'+seft.param)
      .then(res => {
        seft.bookd = res;
        return;
      }).catch(err => {
        Vue.jsonp('https://api.douban.com/v2/book/isbn/'+seft.param)
        .then(res => {
          seft.bookd = res;
          return;
        }).catch(err => {
          seft.$router.push({path:'/booklist/'+seft.param});
          return;  
        });

      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  text-align: left;
}
.box{
  width: 80%;
  margin: 0 auto;
  padding-top:50px; 
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
  width: auto;
}
li {
  display: inline-block;
  margin: 0px;
  vertical-align: top;
}
.paddin_3{
  padding: 3px;
}
a {
  color: green;
  padding:2px;
  margin-right: 3px; 
  cursor: pointer;
}
a:hover{
  color: white;
  background-color: skyblue;
}
p{
  margin: 5px;
  text-align: left;
}
#bookPic{
  width: 170px;
}
.alignL{
  text-align:left;
}
.color_yg{
  color: yellowgreen;
}
.backColor{
  margin-right: 20px;
  background-color: skyblue;
}
pre{
  font-size: 16px;
  white-space: pre-wrap;
  word-wrap: break-word; 
  line-height: 150%;
  text-indent: 2em;
}
</style>



