<template>
  <div class="hello">
    <div class="box">
      <ul>
        <li v-for="i in bookd">
          <div class="bookPic">
            <img v-bind:src="i.image"
            v-on:click="bookinfo(i.id)">
          </div>
          <p class="p_width">
            <a v-on:click="bookinfo(i.id)">{{i.title}}</a>
          </p>
          <p class="p_width">{{i.author}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import VueJsonp from 'vue-jsonp'
Vue.use(VueJsonp)

export default {
  name: 'BookList',
  data () {
    return {
      bookd:[],
      param:''
    }
  },
  beforeCreate(){
    let seft = this;
    seft.param = seft.$route.params.key;
    Vue.jsonp('https://api.douban.com/v2/book/search?q='+seft.param)
    .then(res => {
      console.log(res,'*-*-*-*-');
      seft.bookd = res.books;
    }).catch(err => {
      console.log('error...',err);
    });
  },
  methods:{
    bookinfo(id){
      this.$router.push({path:'/bookinfo/'+id});
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  text-align: left;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
  width: auto;
}
li {
  display: inline-block;
  margin: 5px;
  padding-bottom: 15px;
  vertical-align: top;
}
a {
  color: #42b983;
  padding:2px;
  margin-right: 3px; 
  cursor: pointer;
}
a:hover{
  color: white;
  background-color: skyblue;
}
.box{
  width: 80%;
  margin: 0 auto;
  padding-top:15px; 
}
.bookPic{
  width: 150px;
  height: 200px;
  margin-bottom: 3px;
  overflow: hidden;
}
.bookPic img{
  display: block;
  width: 150px;
  cursor: pointer;
}

.p_width{
  width: 150px;
  font-size: 15px; 
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-top: 2px;
  padding-bottom: 2px;
  margin: 0;
}


</style>



