import Vue from 'vue'
import Router from 'vue-router'
import BookInfo from '@/components/BookInfo'
import BookList from '@/components/BookList'

Vue.use(Router)

export default new Router({
  routes: [
    {
    	path: '/bookinfo/:id',
    	name: 'bookinfo',
    	component: BookInfo
    },
    {
    	path: '/booklist/:key',
    	name: 'booklist',
    	component: BookList
    }
  ]
})



